<template>
  <div id="sales">
    <div v-if="type == 1 || type == 2 || type == 3">
      <el-table
      key="table"
      :data="orderList"
      style="width: 100%">
      <el-table-column
        prop="order_id"
        label="订单号">
      </el-table-column>
      <el-table-column
        prop="publisher"
        label="卖方">
      </el-table-column>
      <el-table-column
        prop="short_name"
        label="商品名称">
      </el-table-column>
      <el-table-column
        prop="price"
        label="价格">
      </el-table-column>
      <el-table-column
        prop="order_createTime"
        label="下单时间">
      </el-table-column>
      <el-table-column
        v-if="type == 3"
        label="状态">
        <template slot-scope="scope">
          {{ scope.row.order_status == 3 ? '已完成' : scope.row.order_status == 4 ? '申诉成功' : '申述失败' }}
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        align="center"
        label="操作"
        width="200">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="orderDetail(scope.row)">详情</el-button>
          <el-button type="text" size="small" @click="receive(scope.row)" v-if="type == 2">收货</el-button>
          <el-button
            type="text"
            size="mini"
            @click="deleteOrder(scope.row, true)" v-if="scope.row.order_status == 1 || scope.row.order_status == 2">取消</el-button>
          <el-button type="text" size="small" @click="appeal(scope.row)" v-if="type == 3 && scope.row.order_status != 4">{{ scope.row.order_status == 3 ? '申诉' : '重新申述' }}</el-button>
          <el-button type="text" size="small" @click="deleteOrder(scope.row)" v-if="type == 3">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    </div>
    <!-- <div v-else-if="type == 3">代收货</div>
    <div v-else-if="type == 4">代收货</div> -->
    <div v-else-if="type == 4">
      <el-table
        key="table1"
        :data="orderList"
        style="width: 100%">
        <el-table-column
          label="申诉流水号"
          prop="appeal_id">
        </el-table-column>
        <el-table-column
          label="订单号"
          prop="order_id">
        </el-table-column>
        <el-table-column
          label="申诉时间"
          prop="createTime">
        </el-table-column>
        <el-table-column
          label="申诉状态">
          <template slot-scope="scope">
            {{ scope.row.appeal_status == 0 ? '申诉中' : scope.row.appeal_status == 1 ? '申诉通过' : '申诉不通过' }}
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="props">
            <el-button
              type="text"
              size="mini"
              @click="handleDetail(props.row)">详情</el-button>
            <el-button
              type="text"
              size="mini"
              @click="cancel(props.row)" v-if="props.row.appeal_status == 0">取消</el-button>
            <el-button
              type="text"
              size="mini"
              @click="deleteAppeal(props.row)" v-if="props.row.appeal_status != 0">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-else-if="type == 11">
      <el-table
        key="table11"
        :data="orderList"
        style="width: 100%">
        <el-table-column
          label="商品 ID"
          prop="goods_id">
        </el-table-column>
        <el-table-column
          label="商品名称"
          prop="short_name">
        </el-table-column>
        <el-table-column
          label="商品图片">
          <template slot-scope="scope">
            <el-image
              v-if="scope.row.image_url"
              style="width: 100px; height: 100px"
              :src="scope.row.image_url"
              :preview-src-list="[scope.row.image_url]"
            />
          </template>
        </el-table-column>
        <el-table-column
          label="发布时间"
          prop="createTime">
        </el-table-column>
        <el-table-column
          label="状态">
          <template slot-scope="scope">
            {{ scope.row.status === 0 ? '上架中' : '下架中' }}
          </template>
        </el-table-column>
        <el-table-column label="操作" align="center">
          <template slot-scope="props">
            <el-button
              size="mini"
              @click="handleEdit(props.$index, props.row)">编辑</el-button>
            <el-button
              size="mini"
              type="danger"
              @click="handleDelete(props.$index, props.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div v-else-if="type == 12">
      <el-table
        key="table12"
        :data="orderList"
        style="width: 100%">
        <el-table-column
          label="订单号"
          prop="order_id">
        </el-table-column>
        <el-table-column
          label="商品创建时间"
          prop="createTime">
        </el-table-column>
        <el-table-column
          label="买方"
          prop="user_nickname">
        </el-table-column>
        <el-table-column
          label="订单状态">
          <template slot-scope="scope">
            {{ scope.row.order_status == 1 ? '待发货' : scope.row.order_status == 2 ? '待收货' : scope.row.order_status == 3 ? '已完成' : scope.row.order_status == 4 ? '对方申诉通过' : scope.row.order_status == 5 ? '对方申述不通过' : '对方申述中' }}
            <span v-if="scope.row.order_status == 1" style="color: #409eff;cursor: pointer;margin-left: 5px;" @click="send(scope.row)">发货</span>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- <div v-else-if="type == ">代收货</div> -->
    <!--分页-->
    <pagination :page="curPage" :limit="pageSize" :total="totalData" @pagination="paginationChange"/>
    <!-- 商品详情 -->
    <el-dialog
      title="商品详情"
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose">
      <div class="item">
        <span>商品名称</span>
        <span>{{ order.short_name }}</span>
      </div>
      <div class="item">
        <span>商品图片</span>
        <span>
          <img width="80" height="80" :src="order.image_url" alt="">
        </span>
      </div>
      <div class="item">
        <span>价格</span>
        <span>{{ order.price }}</span>
      </div>
      <div class="item">
        <span>卖家</span>
        <span>{{ order.publisher }}</span>
      </div>
      <div class="item">
        <span>商品发布时间</span>
        <span>{{ order.createTime }}</span>
      </div>
      <div class="item"></div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 申诉输入框 -->
    <el-dialog
      title="申诉原因"
      :visible.sync="dialogVisible1"
      width="30%"
      :before-close="handleClose">
      <el-input
        type="textarea"
        :rows="2"
        placeholder="请输入申诉原因"
        v-model="textarea">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="appealConfirm">确 定</el-button>
      </span>
    </el-dialog>
    <!-- 申诉原因 -->
    <el-dialog
      title="申诉原因"
      :visible.sync="dialogVisible2"
      width="30%"
      :before-close="handleClose">
      <div class="item">
        {{ order.reason || '无填写原因' }}
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible2 = false">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import {mapState} from 'vuex';
  import {mapActions} from 'vuex'
  import { getOrders, getGoodsDetail, changeOrder, getAppeal, changeAppeal } from '../../../api/index'
  import pagination from '@/components/Pagination/index'
  import {deleteRecomGoods} from './../../../api/index';

  export default {
    data() {
      return {
        orderList: [],
        order: {},
        type: '', // 订单类型
        curPage: 1,
        pageSize: 10,
        totalData: 0,
        dialogVisible: false,
        dialogVisible1: false,
        dialogVisible2: false,
        textarea: ''
      }
    },
    components: {
      pagination
    },
    computed: {
      ...mapState(['userInfo'])
    },
    watch: {
      $route() {
        this.type = this.$route.query.type
        this.getOrderList()
      }
    },
    mounted() {
      this.type = this.$route.query.type
      this.getOrderList()
    },
    methods: {
      getOrderList() {
        this.orderList = []
        if (this.type == 4) {
          let params = {
            page: this.curPage,
            limit: this.pageSize,
            appealer_id: this.userInfo.id
          }
          getAppeal(params).then(res => {
            this.orderList = res.message
            this.totalData = res.total
          })
          return
        }
        let params = {
          page: this.curPage,
          limit: this.pageSize,
          user_id: this.userInfo.id,
          type: this.type
        }
        if (this.type == 12) {
          params.publisher = this.userInfo.user_nickname
        }
        getOrders(params).then(res => {
          res.message.map(item => {
            item.order_createTime = item.createTime
            getGoodsDetail({goodsNo:item.goods_id}).then(res1 => {
              res1.message[0].price = res1.message[0].price / 100
              this.orderList.push(Object.assign(item, res1.message[0]))
            })
          })
          this.totalData = res.total
        })
      },
      orderDetail(item) {
        this.dialogVisible = true
        this.order = item
      },
      appeal(item) {
        this.order = item
        this.dialogVisible1 = true
      },
      handleEdit(index, row) {
        window.localStorage.setItem('goodsInfo',JSON.stringify(row))
        this.$router.push('/me/adminupdate');
      },
      send(item) { // 发货
        this.$confirm('确认发货吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeOrder({
            order_id: item.order_id,
            order_status: 2
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('发货成功')
              this.getOrderList()
            }
          })
        }).catch(() => {         
        })
      },
      async handleDelete(index, row) {
        this.$confirm('您确定永久删除该商品吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then( async() => {
		  let result = await deleteRecomGoods(row.goods_id);
          if(result.success_code === 200){
            this.$message({
              type: 'success',
              message: '已删除'
            });
            location.reload()
          }
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });
        });
      },
      appealConfirm() {
        changeOrder({
          order_id: this.order.order_id,
          order_status: 5,
          reason: this.textarea
        }).then(res => {
          if (res.success_code === 200) {
            this.$message.success('申诉提交成功')
            this.dialogVisible1 = false
            this.textarea = ''
            this.getOrderList()
          }
        })
      },
      handleDetail(item) {
        this.order = item
        this.dialogVisible2 = true
      },
      cancel(item) {
        this.$confirm('确认取消申诉吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeAppeal({
            appeal_id: item.appeal_id,
            type: 0
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('取消申诉成功')
              this.getOrderList()
            } else {
              this.$message.error('取消申诉失败')
            }
          })
        }).catch(() => {         
        })
      },
      deleteAppeal(item) {
        this.$confirm('确认删除申诉吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeAppeal({
            appeal_id: item.appeal_id,
            type: 2
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('删除成功')
              this.getOrderList()
            }
          })
        }).catch(() => {         
        })
      },
      deleteOrder(item, flag) {
        this.$confirm(`确认${flag ? '取消':'删除'}该订单吗`, {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeOrder({
            order_id: item.order_id,
            order_status: 13
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success(`${flag ? '取消':'删除'}订单成功`)
              this.getOrderList()
            }
          })
        }).catch(() => {         
        })
      },
      receive(item) {
        this.$confirm('确认收货吗', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          changeOrder({
            order_id: item.order_id,
            order_status: 3
          }).then(res => {
            if (res.success_code === 200) {
              this.$message.success('收货成功')
              this.getOrderList()
            }
          })
        }).catch(() => {         
        })
      },
      paginationChange(data) { // 分页改变
        this.curPage = data.page
        this.pageSize = data.limit
        this.getOrderList()
      },
      handleClose(done) {
        done()
      }
    },
  }
</script>

<style scoped>
#sales {
  padding-top: 20px;
}
.item {
  display: flex;
  justify-content: space-between;
  margin: 10px 0;
}
.item span:first-child {
  font-size: 16px;
  font-weight: bold;
  color: #333;
}
</style>



